import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { IValidationFormControlOptions, MessageType } from './validation-form-control-options.types';
import { RX_VALIDATION_FORM_CONTROL } from './validation-form-control.constant';

@Component({
  selector: 'rx-validation-form-control',
  templateUrl: './validation-form-control.component.html'
})
export class ValidationFormControlComponent extends ValueAccessor<string> implements IFormControlComponent {
  @Input()
  options: IValidationFormControlOptions;

  @Output()
  events: EventEmitter<any> = new EventEmitter();

  messageType = MessageType;

  correctIssue() {
    this.events.emit({
      type: RX_VALIDATION_FORM_CONTROL.events.correctValidationIssue,
      payload: {
        propertyName: this.options.propertyName,
        componentGuid: this.options.componentGuid
      }
    });
  }
}
